.bg-primary {
    /* background-color: #2883EB !important; */
    background: rgb(86,85,192);
    background: linear-gradient(90deg, rgba(86,85,192,1) 0%, rgba(119,119,255,1) 25%, rgba(40,131,235,1) 51%, rgba(0,212,255,1) 100%);
    border-color: transparent;
    color: white;
}

.text-baby-blue {
    color: #36CBFB !important;
    outline: none !important;
    border: none !important;
}

.btn-primary {
    background-color: #7777ff !important;
    outline-color: #7777ff;
    border-color: #7777ff; 
}

.btn {
    outline-color: transparent;
}

.btn-grad {
    background: rgb(255, 255, 255);
    background: linear-gradient(to right, rgba(40,131,235,1) 51%, rgba(0,212,255,1) 100%);
    border-color: transparent;
    color: white;
}

.btn-purple {
    border-color: linear-gradient(90deg, rgba(77,76,168,1) 0%, rgba(131,131,199,1) 50%);;
    background: linear-gradient(90deg, rgba(77,76,168,1) 0%, rgba(131,131,199,1) 50%);
    outline-color: linear-gradient(90deg, rgba(77,76,168,1) 0%, rgba(131,131,199,1) 50%);;
    color: white
}

.btn-blue {
    background:linear-gradient(90deg, rgba(40,131,235,1) 0%, rgba(36,183,231,1) 50%);;
    background: linear-gradient(90deg, rgba(40,131,235,1) 0%, rgba(36,183,231,1) 50%);
    border-color: linear-gradient(90deg, rgba(40,131,235,1) 0%, rgba(36,183,231,1) 50%);;
    color: white
}

.btn-grad:hover {
     color: white; 
}

.btn-purple:hover {
    color: white; 
}

.btn-blue:hover {
    color: white; 
}

.text-primary {
    color: #2883EB !important;
}